<template>
	<div class="notfound">
    <h1>
      404 NotFound
    </h1>
    <span>
      你找的页面已经飞向了外星球。
    </span>
    <div class="color"></div>
  </div>
</template>

<script>
  export default {
    name: "NotFound"
  }
</script>

<style scoped>
  h1 {
    font-size: 5em;
    display: inline-block;
  }

  .color {
    width: 300px;
    height: 300px;
    background-color: transparent;
    position: absolute;
    top: calc(50% - 150px);
    left: calc(50% - 150px);
    /*border: 1px solid #fff;*/
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;

    box-shadow: inset 0px 0px 50px #fff,
    inset 10px 0px 80px #f0f,
    inset -10px 0px 80px #0ff,
    inset 10px 0px 100px #f0f,
    inset -10px 0px 100px #0ff,
    0px 0px 50px #fff,
    -10px 0px 80px #f0f,
    10px 0px 80px #0ff;
  }
</style>



