import Vue from 'vue'
import Router from 'vue-router'
import Home from '../views/Home'
import FrontHistory from "@/views/FrontHistory";
import FrontToDo from "@/views/FrontToDo";


Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path:'/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'home',
      redirect: '/home/fronthistory',
      component:Home,
      children:[
        {
          path: '/home/fronthistory',
          name: 'fronthistory',
          component: FrontHistory
        },
        {
          path: '/home/fronttodo',
          name: 'FrontToDo',
          component: FrontToDo
        }
      ]
    },
    {
      path: '/frames',
      name: 'frames',
      component: () => import('../views/Frames')
    },
    {
      path: '/javascript',
      name: 'javascript',
      component: () => import('../views/JavaScript'),
      children:[
        {
          path: '/javascript/jshistory',
          name: 'JsHistory',
          component: () => import('../views/JsHistory')
        },
        {
          path: '/javascript/jsdevelopment',
          name: 'JsDevelopment',
          component: () => import('../views/JsDevelopment')
        },
        {
          path: '/javascript/jsnow',
          name: 'JsNow',
          component: () => import('../views/JsNow')
        }
      ]
    },
    {
      path: '/jquery',
      name: 'jquery',
      component: () => import('../views/jQuery')
    },
    {
      path: '/nodejs',
      name: 'nodejs',
      component: () => import('../views/Nodejs')
    },
    {
      path: '/common',
      name: 'common',
      component: () => import('../views/Common')
    },
    {
      path: '/webpack',
      name: 'webpack',
      component: () => import('../views/Webpack')
    },
    {
      path: '/notfound',
      name: 'notfound',
      component: () => import('../views/NotFound')
    },
    {
      path: '*',
      redirect (to){
        if(to.path === '/'){
          return '/home'
        } else {
          return 'NotFound'
        }
      }
    }
  ]
})



