<template>
  <ul class="listmenu">
    <div class="name">JavaScript</div>
      <router-link tag="li" to="/javascript/jshistory">Js的前世</router-link>
      <router-link tag="li" to="/javascript/jsnow">Js的今生</router-link>
      <router-link tag="li" to="/javascript/jsdevelopment">Js的未来</router-link>
  </ul>
</template>

<script>
  import JsHistory from "@/views/JsHistory";
  import JsNow from "@/views/JsNow";
  import JsDevelopment from "@/views/JsDevelopment";
  export default {
    name: "jslist",
    data(){
      return {
        a : 'Js的历史',
        b : 'Js的现在',
        c : 'Js的未来'
      }
    },
    components:{
      JsHistory,
      JsNow,
      JsDevelopment
    },
  }
</script>

<style scoped>
  .listmenu{
    font-family: Microsoft YaHei, Helvetica, Arial, sans-serif;
    font-weight: revert;
    position: relative;
    max-height: 0;
    top:0;
    left: 0;
    display: flex;
    flex-direction: column;
    list-style: none;
    perspective: 400px;
  }
  .name{
    margin-bottom: 5px;
  }
  .listmenu>li{
    padding-top: 5px;
    color: #000;
    font-size: 1em;
    background: linear-gradient(#999,#282828);
    border-left: 3px solid rgba(255,255,255,0);
    visibility: hidden;
    transform: rotate3d(0,1,0,90deg);
    transition: all 0.5s;
  }
  li:hover{
    border-left: 3px solid rgba(255,255,255,1);
    color: #fff;
    background: #282828;
  }
  .listmenu>li:nth-child(1){
    transition-delay: 0ms;
  }
  .listmenu>li:nth-child(2){
    transition-delay: 100ms;
  }
  .listmenu>li:nth-child(3){
    transition-delay: 200ms;
  }
  .listmenu>li:nth-child(4){
    transition-delay: 300ms;
  }
  .listmenu>li:nth-child(5){
    transition-delay: 400ms;
  }

</style>



