<template>
  <div class="hello">
        {{ msg }}
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'JavaScript已经进入到服务器端，未来将在前端与网络层的交互起着重要的作用',
    }
  }
}
</script>

