<template>
	<div class="common">
    <div class="block">
      <span class="demonstration">js社区</span>
      <el-carousel height="150px">
        <el-carousel-item v-for="item in arr" :key="item">
          <h3>{{ item }}</h3>
        </el-carousel-item>
      </el-carousel>
    </div>

    <transition name="el-fade-in">
        <div v-show="show" class="transition-box">
          {{ nas }}
        </div>
    </transition>
  </div>
</template>

<script>
  export default {
    name: "common",
    data(){
      return {
        arr: ['CMD','AMD','Commonjs','Less','Sass','Stylus'],
        show: false,
        nas: '这里之所以提社区是因为在ES6出现之前，js社区群策群力做出很惊人的进步，使得前端的工程化和模块成为可能，其中commonjs只是一个代表，因为nodejs就采用了它的引入方式 require()活跃在服务器中'
      }
    },
    created(){
      this.changShow();
    },
    methods: {
      changShow() {
        setTimeout(() => {
          this.show = true
        }, 500)
      }
    },
    transitions:{
      props:{
        name: "el-fade-in",
        duration: 1000
      }
    }
  }
</script>

<style scoped>
  .el-carousel{
    margin-top: 20px;
    box-shadow: 0 0 13px #00ffff;
  }

  .el-carousel__item h3 {
    color: #eee;
    font-size: 3em;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #282828;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #282828;
  }

  span{
    font-size: 2em;
  }

  .transition-box {
    margin-top: 40px;
  }
</style>



