<template>
	<div class="nodejs">
    <span class="info">
      Node.js的横空出世着实让后端小伙伴的头发稍稍松了一口气。
    </span>
    <div class="article">
      <p>
        对于前端来说，这是兴奋的创举，从此可以直接用ECMAScript语言直接书写在服务器，最终自己完成一个项目。
      </p>
      <p>
        对于复杂的逻辑，所有的交互几乎都可以丢给前端开发人员，当然你的前端得是全栈。
      </p>
      <p>
        因为真正的意义上的前端并不包括dao层，server层，甚至动态的交互他们也只是完成对dao层的接口请求，仅此而已。
      </p>
      <p>
        node还有模块化和框架，用它书写开启服务器的代码也仅仅不过4行，其中一行用来引入一个模块用。
      </p>
      <p>
        Node从选择用ECMAScript语言那天起，就是为了前端的全栈而设立，因为它不需要再另外学习一种新的语言。同时因为JS的强大字符串处理能力以及所以语言中有空前的对数组处理的各种静态与实例方法，它极其适合作为除了数据部分的后端工作，这就是node选择它的原因。
      </p>
      <p>
        node同时也选用了谷歌浏览器 chrome 变态的V8引擎为前端服务。
      </p>
    </div>

  </div>
</template>

<script>
  export default {
    name: "Nodejs"
  }
</script>

<style scoped>
  .nodejs{
    padding: 20px;
    font-family: Microsoft YaHei, Helvetica, Arial, sans-serif;
    font-weight: revert;
  }
  .info{
    /*background:#E6A23C;*/
    padding: 8px;
    font-size: 3em;
    border-radius: 8px;
    color: #fff;
    text-shadow: 2px 2px 0.1em #ffffff;
  }
  .article {
    margin: 120px auto 0;
    border: 2px solid #E6A23C;
    width: 80%;
    color: yellow;
    padding: 40px;
  }

  p{
    font-size: large;
  }
</style>



