<template>
  <div>
    <ul>
      <li>2005年ECMAScript发布新版本ES6</li>
      <li>ES6使得js有了模块化</li>
      <li>js成为真正标准意义的语言</li>
      <li>而webpack让前端进入工程化</li>
      <li>这一切得基于Nodejs的中央仓库</li>
      <li>前端工程师不再需要向以往一样拿一块块js代码寒酸地贴在html文件里</li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "JsNow"
  }
</script>

