<template>
	<div class="webpack">
    <el-row>
      <el-col :span="24"><div class="grid-content bg-purple-dark">
        <h3>
          webpack解决了哪些问题
        </h3>
      </div></el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="8"><div class="grid-content bg-purple">
        <h4>
          前端工程化
        </h4>
        <p>
          随着前端的功能越来越复杂，细节越来越多，特别是nodejs的介入，前端可以自己完成一个完整的项目。所以前端工程化需求越来越高。前端工程化是根据业务特点，将前端开发流程规范化，标准化，它包括了开发流程，技术选型，代码规范，构建发布等，用于提升前端工程师的开发差价率和代码质量。
        </p>
      </div></el-col>
      <el-col :span="8"><div class="grid-content bg-purple">
        <h4>
          前端模块化
        </h4>
        <p>
          前端的模块化两部分：1，js模块化；2，css模块化；模块化是指将相同的代码块抽离，有效减少代码的冗余，提高代码的复用。这两部分都可以用前端自动化构建工具webpack的plugin自动实现。
        </p>
      </div></el-col>
      <el-col :span="8"><div class="grid-content bg-purple">
        <h4>
          组件化
        </h4>
        <p>
          说到组件化就一定要提到webpack.webpack是组件化的最典型的代表，它将一切都视为组件，从入口进出口出。因为它选择用js作为入口文件，那么css和Html文件它是不识别的，但js用了两个方法识别且能够引入进js当中，就是loader和plugin。入口、出口、loader、plugin就形成了webpack的四个组成部分。从此前端真正的能够参与到大型项目的开发当中。
        </p>
      </div></el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: "Webpack"
  }
</script>

<style scoped>
  .webpack{
    font-family: Microsoft YaHei, Helvetica, Arial, sans-serif;
    font-weight: revert;
  }
  el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  h3{
    margin-top: 4px;
  }
  .bg-purple-dark {
    background: #303030;
    padding-top: 5px;
    margin-bottom: 15px;
  }
  .bg-purple {
    background: #606060;
    padding: 5px 10px;
  }

  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  p{
    line-height: 30px;
  }

</style>



