<template>
	<div class="home">
    <ul>
      <router-link tag="li" to="/home/fronthistory">前端的由来</router-link>
      <router-link tag="li" to="/home/fronttodo">前端是做什么的</router-link>
    </ul>
    <div class="content">
      <router-view/>
    </div>

  </div>
</template>

<script>
  import FrontHistory from "@/views/FrontHistory";
  import FrontToDo from "@/views/FrontToDo";
  import calendar from "@/components/calendar";
  export default {
    name: "Home",

    components:{
      FrontHistory,
      FrontToDo,
      calendar
    }
  }
</script>

<style scoped>
  ul{
    display: flex;
    justify-content: space-around;
    list-style: none;
  }
  ul>li{
    color: #bbbbbb;
    font-family: 'Microsoft YaHei', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 2em;
    transition: all .3s;
    cursor: pointer;
  }
  .router-link-active{
    color: #fff;
    transform: scale(1.4);
    transition: all .3s;
  }
  .content{
    padding-top: 1em;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

</style>



