<template>
  <el-container>
    <el-aside width="200px">它从哪里来</el-aside>
    <el-main>
      <p>
        最初所有的开发工作都是由后端工程师完成，随着业务量越来越大，工作繁杂，后端工程师不堪重负，于是将可视化和部分交互功能从开发工作剥离出来，形成了前端开发。
      </p>
      <p>
        后端工程师就主要负责服务器的数据逻辑与业务逻辑。
      </p>
    </el-main>

  </el-container>
</template>

<script>
  export default {
    name: "FrontHistory"
  }
</script>

<style scoped>
  .el-container{
    font-size: large;
    font-family: Microsoft YaHei, Helvetica, Arial, sans-serif;
  }
  .el-aside {
    color: #0ff;
    text-shadow: 0 0 8px #0ff;
    text-align: center;
    line-height: 200px;

  }

  .el-main {
    color: #fff;
    text-align: left;
    line-height: 50px;
  }

</style>



