<template>
	<div class="jq">
    <transition name="el-zoom-in-top">
      <el-header v-show="show2" >jQuery</el-header>
    </transition>
    <el-container>
      <transition name="el-zoom-in-center">
        <el-aside v-show="show2" >Extend</el-aside>
      </transition>
      <transition name="el-zoom-in-bottom">
        <el-main v-show="show2" >
          <p>
            JavaScript库，在原型链prototype上编程后第一次解放前端的双手，前端的第一个利器工具，它的出现对JavaScript的意义重大~~！
            前端开发人员从没有像现在这样可以随心所欲地在网页玩出一朵花，第一次接触jQuery的人，都会觉得简直无所不能！
          </p>
          <p>
            jQuery是一个快速的，简洁的javaScript库，使用户能更方便地处理HTML documents、events、实现动画效果，并且方便地为网站提供Ajax(网络异步请求）交互。其中封装的Ajax直到现在一直被喜爱的人们使用。
          </p>
          <p>
            jQuery提供的Extend对ECMAScript的发展有着深远的启发意义~！
          </p>
          <p>
            本页面还原了jQuery的动画效果，甚至除了home页面外，这些全都是异步请求而来的……向jQuery致敬~！
          </p>
        </el-main>
      </transition>
    </el-container>
  </div>
</template>

<script>
  export default {
    name: "jQuery",
    data : () => {
      return {
        show2: false
      }
    },
    created(){
      this.show();
    },
    methods:{
      show (){
        setTimeout(() => {
          this.show2 = true
        },300)
      }
    }
  }
</script>

<style scoped>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
    font-size: 30px;
    font-weight: bolder;
  }

  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 300px;
  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: left;
    line-height: 30px;
  }

</style>



