<template>
  <el-container>
    <el-aside width="200px">前端是做什么的</el-aside>
    <el-main>
      <p>
        前端工程师主要负责的工作就是用HTML CSS JavaScript将产品UI设计稿实现成网站产品。涵盖用户PC端移动端网页处理视觉与交互问题。从广义来讲，只要是与视觉和交互有关的部分都是前端工程师的专业领域。
      </p>
      <p>
        现在前端的发展已经一样可以负责服务器的数据逻辑与业务逻辑，也就是很流行的全栈工程师。
      </p>
    </el-main>
  </el-container>
</template>

<script>
  export default {
    name: "FrontToDo"
  }
</script>

<style scoped>
  .el-container{
    font-size: large;
    font-family: Microsoft YaHei, Helvetica, Arial, sans-serif;
  }
  .el-aside {
    color: #0ff;
    text-shadow: 0 0 8px #0ff;
    text-align: center;
    line-height: 200px;

  }

  .el-main {
    color: #fff;
    text-align: left;
    line-height: 50px;
  }
</style>



