<template>
  <div id="app">
    <header>
      <div class="logo" @click="handle">Brief History Of Front End</div>
      <ul class="menu">
        <router-link tag="li" to="/home">Home</router-link>
        <router-link tag="li" to="/javascript">
          <jslist></jslist>
        </router-link>
        <router-link tag="li" to="/jquery">jQuery</router-link>
        <router-link tag="li" to="/common">Commonjs</router-link>
        <router-link tag="li" to="/webpack">Webpack</router-link>
        <router-link tag="li" to="/nodejs">Nodejs</router-link>
        <router-link tag="li" to="/frames">Frames</router-link>
        <router-link tag="li" to="/notfound">NotFound</router-link>
      </ul>
    </header>
    <div class="content">
      <router-view/>
    </div>
  </div>
</template>

<script>
  import home from '@/views/Home';
  import javascript from '@/views/JavaScript';
  import jquery from '@/views/jQuery';
  import common from '@/views/Common';
  import webpack from '@/views/Webpack';
  import nodejs from '@/views/Nodejs';
  import frames from '@/views/Frames';
  import NotFound from '@/views/NotFound';
  import jslist from "@/components/jslist";
export default {
  name: 'App',

  components: {
    home,
    javascript,
    jquery,
    common,
    webpack,
    nodejs,
    frames,
    NotFound,
    jslist
  },
  methods:{
    handle(){
      this.$router.push('/')
    }
  }
}
</script>

<style>
  body{
    margin:0;
    padding: 0;
    background: #282828;
  }
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    margin: 0;
    padding: 0;
    height: 100%;
  }
  #app>header{
    display: flex;
    justify-content: space-around;
    align-items: center;
    vertical-align: middle;
    height: 75px;
    padding: 0 10px;
    font-weight: bolder;
    font-size: 1.2em;
    color: #bbb;
    margin-bottom: 25px;
    box-shadow: 0 0 15px #fff;
  }
  .menu{
    display: flex;
    list-style: none;
  }
  .logo,.menu>li{
    height: 30px;
  }
  .logo,.menu>li:hover{
    cursor: pointer;
  }
  .menu>li{
    padding: 0 22px;
    transition: all 0.3s;
  }
  .logo,.menu>li:hover{
    color: #fff;
  }
  .router-link-active:hover .listmenu>li{
    visibility: visible;
    transform: rotate3d(0,1,0,0deg);
  }
  .content{
    margin-top: 25px;
    color: #fff;
  }
  .router-link-active{
    color: #fff;
    transform: scale(1.2);
  }
  .content{
    width: 80%;
    margin: 0 auto;
  }
</style>



