<template>
  <div class="frames">
    框架有丰富的API可以使用，自身都具有模块化，组件化，使代码书写与复用更加便捷。
    同时还有无数丰富的库可以调用。使API使用起来更加简单。
    <div>
      <img src="../assets/angular.svg" alt="Angular">
      <img src="../assets/Vue.svg" alt="Vue">
      <img src="../assets/React.svg" alt="React">
    </div>

    <p>
      在单页面应用流行的今天，用框架开发是很容易的。
    </p>
    <p>
      因为无论跳转到哪个路径，页面都不会刷新，而且响应迅速。
    </p>
    <p>
      组件的使用也更加便捷，下面是一个刚刚写好的日历组件。
    </p>
    <el-button type="primary" round @click="handleClick">调用日历</el-button>
    <transition name="el-fade-in-linear">
        <calendar v-show="show"/>
    </transition>
  </div>
</template>

<script>
  import calendar from "@/components/calendar";
  export default {
    name: "Frames",
    data(){
      return {
        show : false,

      }
    },
    components: {
      calendar
    },
    methods: {
      handleClick(){
        if(this.show) {
          this.show = false;
        } else {
          this.show = true;
        }
      }
    }
  }
</script>

<style scoped>
  button{
    margin-bottom: 40px
  }
  img{
    width: 200px;
    height: 200px;
  }
</style>



