<template>
  <div>
    <ul>
      <li>1995年,Brendan Eich，写出了一个叫LiveScript的脚本语言，正式发布前改名叫JavaScript</li>
      <li>从此互联网进入动态页面时代</li>
      <li>各个浏览器竞相追逐，其中就包括微软。</li>
      <li>但JavaScript 并没有一个标准来统一，这使得JavaScript的标准化势在必行</li>
      <li>1997年欧洲计算机制造商协会（ECMA），定义了名为ECMAScript的全新脚本语言。</li>
      <li>ECMAScript推助着互联网进入全盛时代</li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "JsHistory"
  }
</script>

