<template>
	<div class="calendar">
		<div class="date-header">
			<div class="prev-month" @click="prevMonth"></div>
			<div class="show-date">{{ year }}年{{ month }}月{{ date }}日</div>
			<div class="next-month" @click="nextMonth"></div>
		</div>
		<div class="date-content">
			<div class="week-header">
				<div v-for="item in ['日','一','二','三','四','五','六']" :key="item">
					{{ item  }}
				</div>
			</div>
			<div class="week-day">
				<div class="every-day" v-for="item in 42" :key="item">
					<div :data-day="item - beginDay" @click="chooseDay" v-if="item - beginDay > 0 && item - beginDay <= hasDay"
					     :class="{'current-day':`${year}+${month}+${item-beginDay}`=== day, 'active-day':`${year}+${month}+${item-beginDay}`===`${year}+${month}+${date}` }">
						{{ item - beginDay }}
					</div>
					<div v-else-if="item - beginDay <= 0" class="other-day">{{ item - beginDay + prevDays }}</div>
					<div class="redundant-day" v-else>{{ item - beginDay - hasDay}}</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
  export default {
    name: "calendar",
    data() {
      return {
        year: null,
        month: null,
        date: null,
        day:null
      }
    },
    created(){
      this.getInitTime();
    },
    computed:{
      beginDay(){
        return new Date(this.year, this.month - 1, 1).getDay();
      },
      hasDay(){
        return new Date(this.year,this.month,0).getDate();
      },
      prevDays(){
        return new Date(this.year,this.month -1 ,0).getDate();
      }
    },
    methods:{
      getInitTime(){
        const date = new Date();
        this.year = date.getFullYear();
        this.month = date.getMonth() + 1;
        this.date = date.getDate();
        this.day = `${this.year}+${this.month}+${this.date}`;
      },
      chooseDay(e){
        this.date = e.target.dataset.day;
      },
      prevMonth(){
        if(this.month === 1){
          this.month = 12;
          this.year --;
        }else{
          this.month --;
        }
        this.computedDay();
      },
      nextMonth(){
        if(this.month === 12){
          this.month = 1;
          this.year ++;
        }else{
          this.month ++;
        }
        this.computedDay();
      },
      computedDay(){
        const allDay = new Date(this.year,this.month,0).getDate();
        if(this.date > allDay) {
          this.date = allDay;
        }
      }
    }
  }
</script>

<style scoped>
	.calendar{
		margin: 0 auto;
		display: flex;
		flex-direction: column;
		width: 400px;
	}
	.date-header{
		width: 100%;
		height: 15px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.prev-month,.next-month{
		width: 0;
		height: 0;
		border-top: 5px;
		border-right: 10px;
		border-bottom: 5px;
		border-left: 10px;
		border-color: transparent;
		border-style: solid;
    border-collapse:collapse;
	}
	.prev-month{
		border-right-color: #007bff;
	}
	.next-month{
		border-left-color: #007bff;
	}
	.date-content{
		padding: 0 10px;
	}
	.week-header{
		background: #007bff;
		color: #fff;
		display: flex;
		justify-content: space-around;
	}
	.week-day{
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-around;
	}
	.every-day{
		box-sizing: border-box;
		width: 50px;
		height: 50px;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 4px;
		line-height: 50px;
	}
	.other-day,.redundant-day{
		color: #aaa;
	}
	.other-day{
		width: 100%;
		height: 100%;
	}
	.current-day{
		width: 100%;
		height: 100%;
		background: #007bff;
		color: #ffffff;
	}
	.active-day{
		box-sizing: border-box;
		width: 100%;
		height: 100%;
		border: 1px solid #fff;
	}
</style>



